import { h } from 'preact'
import { Component } from 'preact'
import ObjectForm from './Forms/Object.jsx'

class AddObject extends Component {
  /**
   * Add object is the page
   */
  render ({ onLoad, id }) {
    if (id) {
      // edit
      return (
        h(ObjectForm,{
          id:id,
          title:"Редактировать Объект",path:"/admin-data?objects",
          successMessage:"Объект успешно отредактирован!",
          confirmText:"Сохранить"}))
    }
    return (
      h(ObjectForm,{ submitFinish:async (res) => {
        if (onLoad) onLoad(res)
        const d = await res.json()
        const { data } = d
        this.setState({ id: data })
      },title:"Добавить Объект", addedId:this.state.id, path:"/admin-data?objects", successMessage:"Объект успешно добавлен!", confirmText:"Добавить" }))
  }
}

export default AddObject