import { h } from 'preact'
import Form, { SubmitForm, SubmitButton, FormGroup, Input, TextArea } from '@depack/form'
import FormImage from './../../Components/FormImage.jsx'
import { loadData } from './../../Components/LoadData.jsx'
import ArticleEditor from './../../ArticleEditor.jsx'
import { ErrorAlert, Success, Col } from './../../../frontend/components/Bootstrap.jsx'
import { LoadingIndicator } from './../../Components/index.jsx'

class CategoryForm extends SubmitForm {
  constructor() {
    super()
    Object.assign(this.state, {
      loading: false,
      data: {},
      hint: 'москва-новостройки',
      article: '',
    })
  }
  async componentDidMount() {
    const editing = !!this.props.id
    if (!editing) return
    this.setState({ editing: true })
    /** @type {Array<Category>} */
    const data = await loadData.bind(this)(`categories&id=${this.props.id}`)
    if (data) {
      const [d] = data
      if (d) this.setState({
        data: d, hint: d.seo, article: d.article,
      })
    }
  }
  render({ confirmText, successMessage, addedId, title, id, onClose, closeText = 'Отмена' }) {
    const { formLoading, error, success, loading,
      editing, article, data } = this.state

    const form = (                           h(Form,{onSubmit:this.submit.bind(this), onChange:() => {
      this.reset()
    }},
      h(FormGroup,{label:"Название",help:"Название для меню слева."},
        h(Input,{value:data.title,name:"title",placeholder:"Москва Новостройки",required:true}),
      ),
      h(FormGroup,{help:this.hint,label:"СЕО Название"},
        h(Input,{value:data.seo,required:true,name:"seo",placeholder:"москва-новостройки"}),
      ),
      h(FormGroup,{label:"Описание",help:"Краткое описание для главной страницы."},
        h(TextArea,{rows:"3",required:true,name:"description",placeholder:"Новая недвижиость в столице России -- это привлекательное предложения для тех, кто собирается строить свое будущее в центре событий."},
          data.description,
        ),
      ),
      h(FormImage,{editing:editing,help:"Картинка, отображаемая на главной странице.",required:true,image:data.cdnImage}),
      h(ArticleEditor,{article:article,name:"article",onSave:(html) => {
        this.setState({ article: html })
        this.reset()
      }}),
      editing && h('input',{'value':id,'type':"hidden",'name':"id"}),

      h(ErrorAlert,{error:error}),
      h(Success,{success:success,message:successMessage}),

      h(SubmitButton,{confirmText:confirmText,loading:formLoading,loadingText:"Загрузка..."}),

      onClose &&
        h('button',{'onClick':onClose,'type':"button",'className':"FormCancelBtn btn btn-secondary"},closeText)
      ,
    ))

    return      h(Col,{},
      title && h('h1',{},title),
      loading && h(LoadingIndicator),
      !loading && form,
    )
  }

  get hint() {
    const hint = this.state.hint
    return `Фраза для поисковой оптимизации, участвующая в адресах страниц, например, knedv.ru/<strong>${hint}</strong>/3х-комнатные-квартиры.`
  }
}

export default CategoryForm